.sliderUp-wrap {
  box-sizing: border-box;
  padding: 0 20px 0 20px;
  max-width: 1024px;
  margin: 0 auto;
}

.slider-buckground {
  background-image: url('./img/Rectangle\ 1750.jpg');
  background-position-x: -230px;
  background-position-y: -120px;
  background-size: cover; 
}

.sliderUp-wrap-button-buck, .sliderUp-wrap-button-forward {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 0;
  height: 60px;
  width: 60px;
  border: 2px solid #67AEFC;
  border-radius: 15px;
  transition: transform 0.2s ease-in-out;
}

.sliderUp-wrap-button-forward-chevron {
  transform: rotate(90deg);
  height: 14px;
  width: 25px;
  background-color: #000;
  mask-image: url("img/Chevron.svg");
}

.sliderUp-wrap-button-buck-chevron {
  transform: rotate(-90deg);
  height: 14px;
  width: 25px;
  background-color: #000;
  mask-image: url("img/Chevron.svg");
}

.sliderUp-wrap-button-buck:hover, 
.sliderUp-wrap-button-forward:hover,
.sliderUp-wrap-button-buck:active, 
.sliderUp-wrap-button-forward:active {
  background-color: #67AEFC;
  transform: scale(1.1);
}

.sliderUp-wrap-button-buck:hover > .sliderUp-wrap-button-buck-chevron, 
.sliderUp-wrap-button-forward:hover > .sliderUp-wrap-button-forward-chevron,
.sliderUp-wrap-button-buck:active > .sliderUp-wrap-button-buck-chevron, 
.sliderUp-wrap-button-forward:active > .sliderUp-wrap-button-forward-chevron {
  background-color: rgb(255, 255, 255);
}

.sliderUp-wrap-buttons {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 200px;
  padding-bottom: 40px;
}

.slick-next:before {
  content: '';
}

.slick-arrow, .slick-next {
  display: none;
  width: 0;
  height: 0;
}

.sliderUp-wrap__h3 {
  padding: 40px 0 20px 0;
  text-align: center;
  font-weight: 800;
  font-size: 23px;
  line-height: 34px;
}

@media (min-width: 640px) {
  .sliderUp-wrap-buttons {
    padding-top: 30px;
  }

  .slider-buckground {
    background-image: url('./img/Rectangle\ 1750.jpg');
    background-position-x: 0;
    background-position-y: -50px;
    background-size: 100%; 
    background-repeat: no-repeat;
  }
}